.register {
  width: 100%;
  height: 100vh;
  background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 1) 100%
    ),
    url("https://netflixjunkie.com/wp-content/uploads/2021/11/Netflix-Background-1-1140x600.jpg");
  background-size: cover;
  position: relative;

  .top {
    .wrapper {
      padding: 1.25rem 3.125rem;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .logo {
        height: 2.5rem;
      }

      .loginButton {
        font-size: 1rem;
        font-weight: 500;
        padding: 0.3125rem 0.9375rem;
        background-color: red;
        cursor: pointer;
        border: none;
        color: white;
        border-radius: 0.3125rem;
      }
    }
  }

  .container {
    width: 100%;
    height: fit-content;
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 15.625rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;

    h1 {
      font-size: 3.125rem;
    }
    h2 {
      margin: 1.25rem;
    }
    p {
      font-size: 1.25rem;
    }
    .clicked {
      font-size: 1.5625rem;
      padding: 0.5rem 0.9375rem;
      text-transform: uppercase;
      background-color: red;
      color: white;
      cursor: pointer;
      border-radius: 0.3125rem;
      border: 0.0625rem solid white;

      &:hover {
        background-color: white;
        color: black;
        width: 23.75rem;
        height: 3.125rem;
      }
    }
  }
}
@media screen and (max-width: 800px) {
  .register {
    max-width: 800px;
    max-height: 1000px;
    background-size: cover;
    .container {
      .h1 {
        font-size: 1.3rem;
      }
      .h2 {
        font-size: 0.8rem;
      }
      .p {
        font-size: 0.6rem;
      }
    }
  }
}
